/* Navigation bar styles */
.navbar {
  background-color: #ffcd02;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
}

.logo {
  display: flex;
  align-items: center;
}

.logo img {
  width: 50px;
  margin-right: 10px;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  gap: 20px;
}

nav ul li {
  display: inline;
}

nav ul li a {
  text-decoration: none;
  color: white;
  font-weight: bold;
}

nav ul li a:hover {
  color: #333;
}

footer.footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 10px 0;
  margin-top: 50px;
  position: relative;
  bottom: 0;
  width: 100%;
}
.cart-page {
  padding: 20px;
}

.cart-item {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #ddd;
  padding: 10px 0;
}

.cart-item-image {
  width: 100px;
  height: auto;
  margin-right: 20px;
}

.cart-item div {
  flex-grow: 1;
}

button {
  padding: 10px;
  background-color: #ffcd02;
  color: #333;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

button:hover {
  background-color: #333;
  color: white;
}
