/* Product grid layout */
.product-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
    padding: 20px;
  }
  
  /* Product card styles */
  .product-card {
    border: 1px solid #ddd;
    padding: 15px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .product-card:hover {
    transform: translateY(-5px);
    transition: all 0.3s ease-in-out;
  }
  
  .product-image {
    width: 100%;
    height: auto;
    border-radius: 10px;
  }
  
  .product-card h3 {
    margin-top: 10px;
    color: #333;
  }
  
  .product-card p {
    color: #666;
  }
  
  button {
    padding: 10px 20px;
    background-color: #ffcd02;
    color: #333;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #333;
    color: white;
  }
  